<template>
  <v-container>
    <!-- (tableEdit, tableEditIndex) in chapters[this.indexes.chapter]
        .outline[this.indexes.outline].outlineTable[this.indexes.table]
        .tableEditForm" -->
    <component
      v-for="(editInput, inputIndex) in editFormObject[editFormIndex]
        .editableForm"
      :key="inputIndex"
      :is="editInput.inputType"
      :label="editInput.label"
      :disabled="isDisable(editInput.editable, inputIndex)"
      :items="editInput.items"
      :id="`${editInput.model}-${editFormIndex}`"
      v-model="editInput.value"
      dense
      outlined
      @keyup="
        isNum(
          editInput.value,
          editFormIndex,
          inputIndex,
          $event,
          editInput.isNumber
        );
        withCalcu(editInput.value, editFormIndex, inputIndex, editInput.model);
      "
    ></component>
    <!-- v-on:keyup="putComma($event)" -->
  </v-container>
</template>
<script>
import numeral from "numeral/src/numeral";
import VTextField from "vuetify/lib/components/VTextField";
import VTextarea from "vuetify/lib/components/VTextarea";
import VSelect from "vuetify/lib/components/VSelect";
export default {
  components: {
    VTextField,
    VTextarea,
    VSelect,
    numeral,
  },
  props: [
    "chapters",
    "dialogEdit",
    "indexes",
    "editFormObject",
    "editFormIndex",
  ],
  data: () => ({
    name: "EditModal",
  }),
  oncreate: () => ({}),
  methods: {
    isDisable(option, index) {
      // check option and index
      if (option) {
        return false;
      } else {
        return true;
      }
      // return true - disable, false - active
    },
    isNum(value, index, index2, event, isnum) {
      if (isnum) {
        var isValid = /^[0-9,.]*$/.test(value);
        if (isValid) {
          value = value.replace(/,/g, ""); // remove commas from existing input
          this.editFormObject[index].editableForm[index2].value =
            this.numberWithCommas(value); // add commas back in
        } else {
          //event.target.value = event.target.value.replace(/[^0-9,.]/g, "");
          this.editFormObject[index].editableForm[index2].value = "";
        }
      }
    },
    numberWithCommas(x) {
      var parts = x.toString().split(".");
      parts[0] = parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return parts.join(".");
    },
    withCalcu(value, editFormIndex, inputIndex, withCompute) {
      value = parseInt(value.replace(/,/g, "")); // remove commas from existing input
      if (withCompute) {
        if (editFormIndex == 17) {
          let total = document
            .getElementById("totalKg-" + editFormIndex)
            .value.replace(/,/g, "");
          let per = (value / total) * 100;
          switch (withCompute) {
            case "bioKg":
              this.editFormObject[17].editableForm[inputIndex + 1].value =
                per.toFixed(2);
              break;
            case "recKg":
              this.editFormObject[17].editableForm[inputIndex + 1].value =
                per.toFixed(2);
              break;
            case "rwpKg":
              this.editFormObject[17].editableForm[inputIndex + 1].value =
                per.toFixed(2);
              break;
            case "forDisKg":
              this.editFormObject[17].editableForm[inputIndex + 1].value =
                per.toFixed(2);
              break;
            case "specKg":
              this.editFormObject[17].editableForm[inputIndex + 1].value =
                per.toFixed(2);
              break;
          }
        }
        // if (editFormIndex == 18) {
        //   let total =
        //     this.chapters[3].outline[1].outlineTable[0].tableData.reduce(
        //       (a, b) =>
        //         parseFloat(a) + (parseFloat(b["bioKg"].replace(/,/g, "")) || 0),
        //       0
        //     );
        //   let dividend = total + value;
        //   let per = (value / dividend) * 100;
        //   console.log(per);
        //   console.log(total);
        //   switch (withCompute) {
        //     case "bioKg":
        //       this.editFormObject[18].editableForm[inputIndex + 1].value =
        //         per.toFixed(2);
        //       break;
        //   }
        // }
      }
    },
  },
  computed: {},
};
</script>