<template>
  <v-stepper alt-labels v-model="e1">
    <!-- START OF STEPPER HEADER-->
    <v-stepper-header>
      <v-stepper-step :complete="e1 > 1" step="1"> Indicator 1 </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 2" step="2"> Indicator 2 </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 3" step="3"> Indicator 3 </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 4" step="4"> Indicator 4 </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 5" step="5"> Indicator 5 </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 6" step="6"> Indicator 6 </v-stepper-step>

      <v-divider></v-divider>

      <v-stepper-step :complete="e1 > 7" step="7"> Indicator 7 </v-stepper-step>
    </v-stepper-header>
    <!-- END OF STEPPER HEADER-->

    <!-- START OF STEPPER CONTENT-->
    <v-stepper-items>
      <v-stepper-content step="1">
