        <!-- START ROW 1 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Open/Controlled dumpsite ceased operation
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[0].radio1" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[0].remarks1"
              @change="(v) => (indicator[0].remarks1 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- END ROW 1 -->
        <br />
        <!-- START ROW 2 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Safe Closure and Rehabilitation Submitted
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[0].radio2" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[0].remarks2"
              @change="(v) => (indicator[0].remarks2 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- END ROW 2 -->
        <br />
        <!-- START ROW 3 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Sanitary Landfill
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[0].radio3" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
            <br />
            <div class="text-subtitle-2">
              Memorandum of Agreement
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[0].radio4" row>
              <v-radio label="Yes" value="1"></v-radio>
              <v-radio label="No" value="2"></v-radio>
              <v-radio label="Not Applicable" value="3"></v-radio>
            </v-radio-group>
            <br />
            <div class="text-subtitle-2">
              Contract
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[0].radio5" row>
              <v-radio label="Yes" value="1"></v-radio>
              <v-radio label="No" value="2"></v-radio>
              <v-radio label="Not Applicable" value="3"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[0].remarks3"
