            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- END ROW 3 -->
        <br />
        <!-- START ROW 4 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Residual Containment Area
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[0].radio6" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
            <div class="text-subtitle-2">Capacity</div>
            <v-text-field outlined></v-text-field>
            <div class="text-subtitle-2">Location</div>
            <v-text-field outlined></v-text-field>
            <br />
            <div class="text-subtitle-1">
              Future disposal area (please check the appropriate box)
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[0].radio7" row>
              <v-radio label="Owned" value="0"></v-radio>
              <v-radio label="Cluster" value="1"></v-radio>
            </v-radio-group>
            <div class="text-subtitle-2">Location</div>
            <v-text-field outlined></v-text-field>
            <div class="text-subtitle-2">Life Span</div>
            <v-text-field outlined></v-text-field>
            <div class="text-subtitle-2">Status</div>
            <v-text-field outlined></v-text-field>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[0].remarks4"
              @change="(v) => (indicator[0].remarks4 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- END ROW 4 -->

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="next()">Next</v-btn>
        </v-card-actions>
      </v-stepper-content>

      <v-stepper-content step="2">
        <div class="text-h4">Materials Recovery Facilities</div>
        <!-- ROW 1 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              <span>*</span>
              At the end of the planning period, all barangays should be service
              by MRF
            </div>
            <div class="text-subtitle-1">
              <span>*</span>
              If utilizing centralized MRF, define which brgys are covered by
              the centralized MRF(e.g. 2 brgys/ 15 total brgys)
            </div>
            <v-radio-group v-model="indicator[1].radio1" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[1].remarks1"
              @change="(v) => (indicator[1].remarks1 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- ROW 1 -->
        <v-card-actions>
          <v-btn @click="e1 = 1">Back </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="next()">Next</v-btn>
        </v-card-actions>
      </v-stepper-content>

      <v-stepper-content step="3">
        <div class="text-h4">Diversion Targets</div>
        <!-- ROW 1 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Above 50% by 2017
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[2].radio1" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
            <div class="text-subtitle-1">
              Increasing diversion target and decreasing disposal targets
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[2].radio2" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[2].remarks1"
              @change="(v) => (indicator[2].remarks1 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- ROW 1 -->
        <v-card-actions>
          <v-btn @click="e1 = 2">Back </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="e1 = 4">Next</v-btn>
        </v-card-actions>
      </v-stepper-content>

      <v-stepper-content step="4">
        <div class="text-h4">
          Segregation at Source and ssegreagated Collection
        </div>
        <!-- ROW 1 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              100% target for the !0th year planning period
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[3].radio1" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[3].remarks1"
              @change="(v) => (indicator[3].remarks1 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- ROW 1 -->
        <v-card-actions>
          <v-btn @click="e1 = 3">Back </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="e1 = 5">Next</v-btn>
        </v-card-actions>
      </v-stepper-content>

      <v-stepper-content step="5">
        <div class="text-h4">Expenditures/ Investment</div>
        <!-- ROW 1 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Presence of Appropriate projected investment costs for strategies
              identified
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[4].radio1" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[4].remarks1"
              @change="(v) => (indicator[4].remarks1 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- ROW 1 -->
        <!-- ROW 2 -->
        <v-row>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Source of fund is indicated
              <span class="red--text">*</span>
            </div>
            <v-radio-group v-model="indicator[4].radio2" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[4].remarks2"
              @change="(v) => (indicator[4].remarks2 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- ROW 2 -->
        <v-card-actions>
          <v-btn @click="e1 = 4">Back </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="next()">Next</v-btn>
        </v-card-actions>
      </v-stepper-content>

      <v-stepper-content step="6">
        <div class="text-h4">Presence of Policy Support</div>
        <!-- ROW 1 -->
        <v-row>
          <v-col sm="6">
            <v-radio-group v-model="indicator[5].radio1" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[5].remarks1"
              @change="(v) => (indicator[5].remarks1 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- ROW 1 -->
        <v-card-actions>
          <v-btn @click="e1 = 5">Back </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" @click="next()">Next</v-btn>
        </v-card-actions>
      </v-stepper-content>

      <v-stepper-content step="7">
        <div class="text-h4">
          Presence of institutional/organizational arrangements
        </div>
        <!-- ROW 1 -->
        <v-row>
          <v-col sm="6">
            <v-radio-group v-model="indicator[6].radio1" row>
              <v-radio label="Yes" value="0"></v-radio>
              <v-radio label="No" value="1"></v-radio>
            </v-radio-group>
          </v-col>
          <v-col sm="6">
            <div class="text-subtitle-1">
              Remarks
              <span class="red--text">*</span>
            </div>
            <v-textarea
              :value="indicator[6].remarks1"
              @change="(v) => (indicator[6].remarks1 = v)"
              outlined
            >
            </v-textarea>
          </v-col>
        </v-row>
        <hr />
        <!-- ROW 1 -->
        <v-card-actions>
          <v-btn @click="e1 = 6">Back </v-btn>
          <v-spacer></v-spacer>
          <v-btn color="error">Return</v-btn>
          <v-btn color="primary">Save</v-btn>
          <v-btn color="success" @click="submit()">Submit</v-btn>
        </v-card-actions>
      </v-stepper-content>
    </v-stepper-items>
    <!-- END OF STEPPER CONTENT-->
  </v-stepper>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      e1: 1,
      name: "FormTwoCom",
      indicator: [
        {
          radio1: null,
          radio2: null,
          radio3: null,
          radio4: null,
          radio5: null,
          radio6: null,
          radio7: null,
          remarks1: null,
          remarks2: null,
          remarks3: null,
          remarks4: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          radio2: null,
          remarks1: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          radio2: null,
          remarks1: null,
          remarks2: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
        {
          radio1: null,
          remarks1: null,
        },
      ],
    };
  },
  created() {
    // let id2 = this.$route.query.id;
    //console.log(id2);
    if (this.$route.query.form_id != null) {
      this.fetchForm2({
        id: this.$route.query.form_id,
      }).then(() => {
        this.setForm2Details();
      });
    }
  },
  methods: {
    ...mapActions(["fetchForm2", "createForm2"]),
    next() {
      this.e1++;
    },
    submit() {
      this.createForm2({
        header_id: this.$route.query.id,
        indicator_com_1_1: this.indicator[0].radio1,
        indicator_rem_1_1: this.indicator[0].remarks1,
        indicator_com_1_2: this.indicator[0].radio2,
        indicator_rem_1_2: this.indicator[0].remarks2,
        indicator_com_1_3: this.indicator[0].radio3,
        indicator_rem_1_3: this.indicator[0].remarks3,
        indicator_com_1_4: this.indicator[0].radio6,
        indicator_rem_1_4: this.indicator[0].remarks4,
        memo_agreement: this.indicator[0].radio4,
        contract: this.indicator[0].radio5,
        disposal_area: this.indicator[0].radio7,

        indicator_com_2_1: this.indicator[1].radio1,
        indicator_rem_2_1: this.indicator[1].remarks1,

        indicator_com_3_1: this.indicator[2].radio1,
        indicator_com_3_2: this.indicator[2].radio2,
        indicator_rem_3_1: this.indicator[2].remarks1,

        indicator_com_4_1: this.indicator[3].radio1,
        indicator_rem_4_1: this.indicator[3].remarks1,

        indicator_com_5_1: this.indicator[4].radio1,
        indicator_rem_5_1: this.indicator[4].remarks1,
        indicator_com_5_2: this.indicator[4].radio2,
        indicator_rem_5_2: this.indicator[4].remarks2,

        indicator_com_6_1: this.indicator[5].radio1,
        indicator_rem_6_1: this.indicator[5].remarks1,

        indicator_com_7_1: this.indicator[6].radio1,
        indicator_rem_7_1: this.indicator[6].remarks1,
      });
    },

    setForm2Details() {
      let newAllChapter = JSON.stringify(this.allForm2);
      let new_data = JSON.parse(newAllChapter);

      this.indicator[0].radio1 = new_data.indicator_com_1_1;
      this.indicator[0].radio2 = new_data.indicator_com_1_2;
      this.indicator[0].radio3 = new_data.indicator_com_1_3;
      this.indicator[0].radio4 = new_data.memo_agreement;
      this.indicator[0].radio5 = new_data.contract;
      this.indicator[0].radio6 = new_data.indicator_com_1_4;
      this.indicator[0].radio7 = new_data.disposal_area;
      this.indicator[0].remarks1 = new_data.indicator_rem_1_1;
      this.indicator[0].remarks2 = new_data.indicator_rem_1_2;
      this.indicator[0].remarks3 = new_data.indicator_rem_1_3;
      this.indicator[0].remarks4 = new_data.indicator_rem_1_4;

      this.indicator[1].radio1 = new_data.indicator_com_2_1;
      this.indicator[1].remarks1 = new_data.indicator_rem_2_1;

      this.indicator[2].radio1 = new_data.indicator_com_3_1;
      this.indicator[2].radio2 = new_data.indicator_com_3_1;
      this.indicator[2].remarks1 = new_data.indicator_rem_3_1;

      this.indicator[3].radio1 = new_data.indicator_com_4_1;
      this.indicator[3].remarks1 = new_data.indicator_rem_4_1;

      this.indicator[4].radio1 = new_data.indicator_com_5_1;
      this.indicator[4].radio2 = new_data.indicator_com_5_2;
      this.indicator[4].remarks1 = new_data.indicator_rem_5_1;
      this.indicator[4].remarks2 = new_data.indicator_rem_5_2;

      this.indicator[5].radio1 = new_data.indicator_com_6_1;
      this.indicator[5].remarks1 = new_data.indicator_rem_6_1;

      this.indicator[6].radio1 = new_data.indicator_com_7_1;
      this.indicator[6].remarks1 = new_data.indicator_rem_7_1;
    },
  },
  computed: {
    ...mapGetters(["allForm2"]),
  },
  watch: {},
};
</script>
