    uploadFilePerOl(chapterIndex, outlineIndex) {
      let count =
        this.chapters[chapterIndex].outline[outlineIndex].uploads[0].uploads
          .length;
      var formData = new FormData();

      if (count > 0) {
        for (var i = 0; i < count; i++) {
          formData.append(
            "files[]",
            this.chapters[chapterIndex].outline[outlineIndex].uploads[0]
              .uploads[i]
          );
        }

        let curr_count =
          this.chapters[chapterIndex].outline[outlineIndex].uploads[0].curr_data
            .length;
        if (curr_count > 0) {
          for (var i = 0; i < curr_count; i++) {
            formData.append(
              "curr_files[]",
              this.chapters[chapterIndex].outline[
                outlineIndex
              ].uploads[0].curr_data[i].new_name.replace(
                "http://swm.emb.gov.ph/swms_api/uploads/",
                //"http://localhost/swms_api/uploads/",
                ""
              )
            );
          }
        }

        formData.append("header_id", sessionStorage.getItem("header_id"));
        formData.append("city_id", sessionStorage.getItem("city_id"));
        formData.append("chapter", chapterIndex + 1);
        formData.append("outline", outlineIndex);

        this.uploadFilePerOutline(formData).then(() => {
          this.fetchAttachPerChapter({
            city: sessionStorage.getItem("city_id"),
            header_id: sessionStorage.getItem("header_id"),
            chapter: chapterIndex + 1,
          }).then(() => {
            this.setAttachment();
          });
        });
      } else {
        swal({
          title: "Attachment field is empty!",
          icon: "error",
          showCloseButton: true,
          dangerMode: true,
        });
      }
    },

